.tile-container{
    width: 300px;
    height: 200px;
    
    float: left;
    padding: 10px;
    margin: 20px 15px 20px 15px;
    -webkit-box-shadow: 0 2px 6px grey;
            box-shadow: 0 2px 6px grey;
    position: relative;
    cursor: pointer;
}

.tile-container > img{
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain;
}



