
input{
	width: 15%;
	display: inline-block;
	padding: 10px 15px;
	font-size: 16px;
	border-radius: 0;
	margin-right: 10px;
	-webkit-appearance: none;
	border: 2px solid #9E9E9E;
	font-family: 'Quicksand';
}

input:focus{
	outline: none;
	width: 30%;
	-webkit-transition: width .25s;
	-o-transition: width .25s;
	transition: width .25s;
}

input:not(:focus){
	width: 20%;
	-webkit-transition: width .25s;
	-o-transition: width .25s;
	transition: width .25s;	
}

button{
	display: inline-block;
	padding: 10px 15px;
	background-color: #fff;
	font-size: 16px;
	border-radius: 0;
	-webkit-appearance: none;
	
	font-weight: bold;
	border: 2px solid #9E9E9E;
}

button:enabled{
	color: black;
	cursor: pointer;
	border: 2px solid black;
}

button:focus{
	outline: none;
}




